.header {
    width: 100%;
    height: 3rem;
    margin: 0;
    position: fixed;
    top: 0;
    z-index: var(--z-fixed);
    background-color: hsl(0deg 93% 90%);
}

.nav {
    height: calc(var(--header-height)+ 1.5rem);
    display: flex;
    padding-right: 1rem;
    padding-left: 1rem;
    justify-content: space-between;
    align-items: center;
    column-gap: 1rem;
}

.nav__logo {
    font-family: "Aboreto";
    font-size: x-large;
}

.nav__logo,
.nav__toggle {
    color: var(--title-color);
    font-weight: var(--font-medium);
}


.nav__list {
    display: flex;
    column-gap: 2rem;
}

.nav__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: var(--small-font-size);
    color: var(--title-color);
    font-weight: var(--font-medium);
    transition: .3s;
}

.nav__icon, 
.nav__close,
.nav__toggle {
    display: none;
}
 
/* Active Link */
.active-link,
.nav__link:hover {
    color: hsl(0deg 100% 47%);;
}

.scroll__header {
    box-shadow: 0 -1px 4px rgba(0,0,0,0.15);
}


/* BREAKPOINTS */

/* For medium devices */
@media screen and (max-width: 768px) {
    .header {                                                                            
        top: initial;
        bottom: 0;
    }

    .nav {
        height: var(--header-height);
    }

    

    .nav__menu {
        position: fixed;
        bottom: -100%;
        left: 0;
        width: 100%;
        background-color: var(--body-color);
        padding: 2rem 1.5rem 4rem;
        box-shadow: 0px -1px 4px rgba(0,0,0,0.15);
        border-radius: 1.5rem 1.5rem 0 0;
        transition: .3s;
    }

    .show-menu {
        bottom: 0;
        z-index: -100;
    }

    .nav__list {
        display: grid;
        grid-template-columns: repeat(3,1fr);
        gap: 2rem;
    }

    .nav__icon {
        font-size: 1.2rem;
    }

    .nav__close {
        position: absolute;
        right: 1.3rem;
        bottom: .5rem;
        font-size: 1.5rem;
        cursor: pointer;
        color: var(--title-color);
    }

    .nav__close:hover {
        color: var(--title-color-dark);
    }

    .nav__toggle {
        font-size: 1.1rem;
        cursor: pointer;
    }

    
    .nav__icon,
    .nav__close,
    .nav__toggle {
        display: block;
    }
}

/* For small devices */
@media screen and (max-width: 350px) {
    .nav__menu {
        padding: 2rem 0.25rem 4rem;
    }
    .nav__list {
        column-gap: 0;
    }
} 